@import '../../../scss/variables-mixins/variables-mixins';

.sectionWrapper {
  padding-top: 100px;
  padding-bottom: 100px;

  p, ul {
    font-size: 13px;
    letter-spacing: .5px;
    margin: 0 20px;
    text-align: left;
  }

  p {
    margin-bottom: 5px;
  }

  ul {
    padding: 20px 0 20px 30px;
  }

  h2, h3, h4, h5 {
    font-size: 13px;
    letter-spacing: 1px;
    margin: 20px;
  }
}
